import { LitElement, html, css } from 'lit';
import { customElement, property, } from 'lit/decorators.js'
import './types/add';
import './types/view';


@customElement("pdb-connections-types")
export class PdbConnectionsTypes extends LitElement {
  constructor() { super(); }

  @property({ type: Object })
  private _property?: any;

  connectedCallback() {
    super.connectedCallback();
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];

  render() {

    return html`
      <div class="flex flex-col">
        <pdb-connections-types-add class="my-1"></pdb-connections-types-add>
        <pdb-connections-types-view class="my-1"></pdb-connections-types-view>
      </div>
    `;
  }

  firstUpdated() { }

  createRenderRoot() {
    return this;
  }
}
